<template>
  <div class="punch_card" @click='outside($event)'>
    <!-- -->
      <div class="card_bg">
        <img src="../../../../components/img/.png" alt="">
      </div>
      <!--  -->
      <div class="card_phone">
        <div class="cardPhone_top">
          <img src="../../../../components/img/phone.png" alt="">
          <span>{{title}}</span>
        </div>
        <p></p>
      </div>
      <!--  -->
     <div class="phone_input"  >
        <input v-model="message" class="input"  oninput = "value=value.replace(/[^\d]/g,'')" placeholder="" name="tel" maxlength="11">
        <span></span>
     </div>
     <!--  -->
     <div class="no_buy">
       <p v-show="no_buy==2">
         <span class="star"></span>
            
         <span></span>
       </p>
     </div>
     <!--  -->
     <div class="btn">
       <button disabled v-show="status==2"></button>
       <button  @click="btn" class="participation" v-show="status==1"></button>
     </div>
  </div>
